import { filter } from 'lodash';
import { defineStore } from 'pinia';
import { reactive } from 'vue';

import axios from 'Axios';

import { noticeStore } from "../stores/notice.js";

export const orderStore = defineStore('orderStore', {
    state: () => {
        return {
            loaded: false,
            orders: {
                proposed: reactive([]),
                reserved: reactive([]),
                prepared: reactive([]),
            },
            disallowAccess: false,
            filters: {},
        }
    },
    actions: {
        loadOrdersData: async function (userID, systemID, locationID) {
            this.loaded = false;

            const url = core.ajaxUrl;
            const data = new FormData();

            data.append('action', core.endpoints.load_orders_data.action);
            data.append('nonce', core.endpoints.load_orders_data.nonce);

            if (userID) {
                data.append('userID', userID);
            }

            if (systemID) {
                data.append('merchantID', systemID);
            }

            if (locationID) {
                data.append('locationID', locationID);
            }

            return await axios.post(url, data).then((resp) => {
                this.loaded = true;
                if (resp.status === 200 && resp.data) {

                    // if we have tampering here, we should set certain things
                    if (!resp.data.status && resp.data?.data?.disallowAccess) {
                        this.loaded = false;
                        Object.assign(this.disallowAccess, resp.data.data.disallowAccess);
                    }

                    Object.assign(this, resp.data.data);
                }
            });
        },
        loadOrderData: async function (userID, systemID, locationID, orderID) {
            const url = core.ajaxUrl;
            const data = new FormData();

            data.append('action', core.endpoints.load_order_data.action);
            data.append('nonce', core.endpoints.load_order_data.nonce);

            if (userID) {
                data.append('userID', userID);
            }

            if (systemID) {
                data.append('merchantID', systemID);
            }

            if (locationID) {
                data.append('locationID', locationID);
            }

            if (orderID) {
                data.append('orderID', orderID);
            }

            return await axios.post(url, data);
        },
        updateOrderData: async function (userID, systemID, locationID, orderID) {
            // first, find current order in store
            const orderData = this.findCurrentOrderInst(orderID);
            const orderStatus = orderData.placement;

            if (orderData.inst.length > 0) {
                // activate the animation to remove it from wherever it is
                orderData.inst[0].changing = true;

                // then remove it from the current array
                this.removeOrder(orderData.type, orderData.inst)

                // then load the new data for it.
                return await this.loadOrderData(userID, systemID, locationID, orderID);
            } else {

            }

            
        },
        findCurrentOrderInst(id) {
            let order = {
                type: null,
                inst: null
            };

            order.inst = this.orders.proposed.filter(el => {
                return el.id === id;
            });

            if (!order.inst) {
                order.inst = this.orders.reserved.filter(el => {
                    return el.id === id;
                });

                order.type = 'reserved';
            }

            // if we havent found it any other array, assume its in proprosed.
            order.type = 'proposed';

            return order;
        },
        removeOrder(type, inst) {
            setTimeout(() => {
                this.orders[type] = this.orders[type].filter(el => {
                    return el.id != inst[0].id;
                })
            }, 1500)
        },
        categorizeOrder(order, type) {
            this.orders[type.toLowerCase()].push(order)
        }
    }
});