<template>
    
    <div v-html="timeTillReady"></div>

</template>

<script>

import * as helpers from '../../helpers/orderHelpers.js';

export default {
    name: "TimeTillReady",
    components:{},
    props:{
        order:{
            type: Object,
            required: true
        }
    },
    computed: {
        timeTillReady: function () {
            const fulfillments = this.order.fulfillments;

            let html = '';
            let remainingTime, readyAt;
            
            if (fulfillments && fulfillments.length > 0) {
                const ful = this.order.fulfillments[0];

                if (ful.type === 'PICKUP') {
                    const lastUpdate = this.order.updated_at;
                    const pickupDeets = ful.pickup_details;

                    // If we have a ready_at timestamp, use that for more accuracy, but if not, use the pickup_at as a fallback
                    if (pickupDeets && 'ready_at' in pickupDeets) {
                        readyAt = new Date(pickupDeets.ready_at);
                    } else {
                        readyAt = new Date(pickupDeets.pickup_at);
                    }

                    if (fulfillments) {
                        const now = new Date();

                        html += '<p>';

                        remainingTime = (readyAt - now) / 1000;

                        if (remainingTime >= 1) {
                            const minutes = (Math.floor((remainingTime / 60)));

                            html += helpers.capitalize(ful.type) + ', ';

                            if (minutes < 1 && minutes > 0) {
                                html += '< 1 min';
                            } else {
                                html += minutes + ' min ';
                            }
                        } else {
                            html += 'Ready for Pickup!';
                        }

                        html += '</p>';
                    }

                    html += '<small>Fulfill by: ' + readyAt.toLocaleString() + '</small>';

                }

                if (ful.type === 'DELIVERY') {
                    const lastUpdate = this.order.updated_at;
                    const pickupDeets = ful.delivery_details;

                    // If we have a ready_at timestamp, use that for more accuracy, but if not, use the pickup_at as a fallback
                    if (pickupDeets && 'ready_at' in pickupDeets) {
                        readyAt = new Date(pickupDeets.pickup_at);
                    } else {
                        readyAt = new Date(pickupDeets.deliver_at);
                    }

                    if (fulfillments) {
                        const now = new Date();

                        html += '<p>';

                        remainingTime = (readyAt - now) / 1000;

                        if (remainingTime >= 1) {
                            const minutes = (Math.floor((remainingTime / 60)));

                            html += helpers.capitalize(ful.type) + ', ';

                            if (minutes < 1 && minutes > 0) {
                                html += ' < 1 min';
                            } else {
                                html += minutes + ' min ';
                            }

                        } else {
                            html += 'Ready for Delivery!';
                        }

                        html += '</p>';

                    }

                    html += '<small>Fulfill by: ' + readyAt.toLocaleString() + '</small>';

                }
            }

            return html;
        }
    },
};
</script>
